/* General Styles */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Inter', 'Arial', sans-serif;
    background-color: #f9f9f9;
}

/* Navigation */
.nav {
    height: 64px;
    background-color: #fff;
    padding: 0 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    max-width: 2000px;
    z-index: 1000;
}

.mobile-menu {
    border-radius: 0 0 10px 10px;
    display: none;
}

.mobile-menu a {
    display: none;
    border-radius: 10px;
}

.mobile-menu a:hover {
    background-color: #CCC;
}

/* Navigation Links */
.nav-left {
    display: flex;
    align-items: center;
    gap: 20px;
}

.nav-links {
    display: flex;
    align-items: center;
    gap: 20px;
}

/* Ensure .nav-right is always on the right */
.nav-right {
    display: flex;
    align-items: center;
    gap: 20px;
    position: absolute;
    right: 30px;
}

/* Ensure it remains visible on all screens */
.nav-link {
    text-decoration: none;
    color: #333;
    font-size: 16px;
    font-weight: 500;
}

.nav-left-logo {
    font-size: 22px;
    font-weight: bold;
    color: #007bff;
}

.sign-up {
    background-color: #007bff;
    color: white;
    padding: 8px 16px;
    border-radius: 5px;
}

.sign-up:hover {
    background-color: #0056b3;
}

/* Hamburger Menu */
.hamburger {
    display: none;
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    padding: 10px;
}

/* Hero Section */
.first-hero-container {
    background: url("../classroom.jpg") no-repeat;
    background-size: cover;
    background-position: center;
    height: auto;
    min-height: 800px;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    text-align: center;
    color: white;
    padding-top: 70px;
    width: 100%;
    max-width: 2000px;
    margin: 0 auto;
}

.first-hero-container .first-hero-content h1 {
    font-size: 60px;
    font-weight: bold;
    padding: 20px;
    width: 70%;
}

.first-hero-container .first-hero-content p {
    font-size: 26px;
    max-width: 600px;
}

.first-hero-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(255, 255, 255, 0.9);
    color: black;
    border-radius: 20px;
    width: 35%;
    padding: 40px;
    margin-left: 50px;
    box-shadow: 0 6px 8px rgba(0, 0, 0, 0.5);
    max-height: 80vh;
    height: 80%;
    overflow: hidden;
}

.first-hero-content h1 {
    display: flex;
    margin: auto;
}

.hero-buttons {
    display: inline;
    width: auto;
}

.hero-buttons button {
    padding: 12px 24px;
    font-size: 18px;
    border-radius: 5px;
    cursor: pointer;
    margin: 20px auto 15px auto;
    border: none;
}

.view-demo:hover {
    background-color: black;
    color: white;
    transition: 0.1s ease-in-out;
}

.sign-up:hover {
    background-color: #00aeff;
    transition: 0.1s ease-in-out;
}

/* Sections */
.second-hero, .why-us, .faq, .about, .contact, .surveys, .blog {
    padding: 60px 20px;
    background-color: white;
    text-align: center;
    border-radius: 8px;
}

h2 {
    font-size: 28px;
    color: #333;
}

ul {
    list-style: none;
    padding: 0;
}

ul li {
    font-size: 18px;
    margin: 8px 0;
}

/* Footer */
.footer {
    background-color: #222;
    color: white;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    max-width: 2000px;
    margin: 0 auto;
}

.footer a {
    color: white;
    text-decoration: none;
}

.footer a:hover {
    text-decoration: underline;
}

/* Mobile Navigation */
@media (max-width: 1024px) {
    .hamburger {
        display: block;
    }

    .nav-links {
        display: none;
    }

    /* Mobile menu expands downward */
    .mobile-menu {
        position: fixed;
        display: flex;
        flex-direction: column;
        top: 64px;
        width: 100%;
        left: 0;
        background: white;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.25);
        overflow: hidden;
        max-height: 0;
        transition: max-height 0.5s ease-in-out;
        z-index: 999;
    }

    .mobile-menu.open {
        max-height: 400px; /* Adjust as needed */
    }

    .mobile-menu a {
        font-size: 20px;
        padding: 30px;
        text-align: left;
        display: block;
    }
}

/* Small mobile */
@media (max-width: 599px) {
    .mobile-menu {
        display: none;
        max-height: 100vh;
        transition: none;
    }

    .mobile-menu.open {
        display: flex;
        height: 100vh;
        max-height: 100vh;
        transition: none;
        border-top: 1px solid #555;
    }

    .mobile-menu a {
        font-size: 16px;
        padding: 20px;
        text-align: left;
        display: block;
    }

    .mobile-menu button {
        padding: 10px;
        margin: 15px 15px 0 15px;
    }
    
    .nav-left-logo {
        font-size: 22px;
    }

    .nav-right-link {
        display: none;
    }

    .sign-up {
        border: none;
    }

    .sign-up:focus {

    }

    .log-in {
        background-color: #DDD;
        color: black;
        padding: 8px 16px;
        border-radius: 5px;
        border: 1px solid #999;
    }
    
    .log-in:focus {
        background-color: #0056b3;
    }

    .first-hero-container {
        background: url("../classroom_phone.jpg") no-repeat center;
        padding-top: 0;
        min-height: 800px;
        display: flex;
        justify-content: center;
    }

    .first-hero-container .first-hero-content h1 {
        font-size: 40px;
        width: auto;
    }

    .first-hero-container .first-hero-content p {
        width: auto;
        font-size: 20px;
    }

    .first-hero-container .first-hero-content button {
        border: none;
        padding: 12px 24px;
        font-size: 18px;
        font-weight: bold;
    }

    .first-hero-content {
        width: 80%;
        margin: auto;
        height: auto;   
    }
}

/* Large mobile */
@media (min-width: 600px) and (max-width: 799px) {
    .first-hero-container {
        min-height: 500px;
    }

    .first-hero-container h1 {
        font-size: 50px;
        width: auto;
    }

    .first-hero-content {
        width: 50%;
        margin-left: 25px;
    }

    .first-hero-container .first-hero-content h1 {
        font-size: 30px;
        font-weight: bold;
        padding: 20px;
        width: 100%;
    }

    .first-hero-container .first-hero-content p {
        font-size: 15px;
    }

    .hero-buttons button {
        padding: 6px 12px;
        font-size: 16px;
        border-radius: 5px;
        cursor: pointer;
        margin: 20px auto 15px auto;
        border: none;
    }
}

/* Tablet */
@media (min-width: 800px) and (max-width: 1280px) {
    .first-hero-container {
        min-height: 600px;
    }

    .first-hero-content {
        height: 60%;
    }
    
    .first-hero-container .first-hero-content {
        width: 40%;
    }

    .first-hero-container .first-hero-content h1 {
        font-size: 40px;
        width: 100%;
    }

    .first-hero-container .first-hero-content p {
        font-size: 20px;
        width: 100%;
    }

    .hero-buttons button {
        padding: 6px 12px;
        font-size: 16px;
        border-radius: 5px;
        cursor: pointer;
        margin: 20px auto 15px auto;
        border: none;
    }
}
